      <p class="PTexts">
        <span style="font-weight: 500;">Work Out:</span> The Work Out content is offered only to the average and mild depression groups. There were significant differences in usage across these groups (t(68) = -2.88, p &lt; 0.05). Similarly, for the time spent using the content, notable differences were observed between the groups (t(68) = 2.78, p &lt; 0.05). We found that the group with average depression levels had higher performance rates compared to the mild group, suggesting that those with average depression are more likely to use the app actively to alleviate their symptoms.
        <br><span style="font-weight: 500;">Finding Blue:</span> Users with severe depression received an average score of 46 in Finding Blue, while those with average depression scored 44, and the mild depression group scored an average of 25, which was notably lower by comparison. ANOVA analysis revealed significant differences across these groups, F(2,135) = 6.49 (p &lt; 0.05). Furthermore, Tukey's post-hoc analysis showed significant differences between the mild and severe groups (p &lt; 0.05), as well as between the mild and average groups (p &lt; 0.05). This suggests that the mild depression group outperformed the others in cognitive control, sensitivity to social evaluation, and avoidance reactivity.
        <br><span style="font-weight: 500;">Digital Mandala:</span> We compared the completion rates and time spent across groups using digital mandalas. However, we found no significant differences, indicating that digital mandalas are not effective in distinguishing between different levels of depression severity. Therefore, it seems necessary to diversify the complexity of the mandala patterns to enhance the content.
